import Vue from 'vue';

Vue.directive('background', {
    bind: function (el, binding) {        
        el.style.backgroundImage = binding.value
    }
});

Vue.directive('gradient', {
    bind: function (el, binding, vnode) {
        // Apply theme color
        let theme = vnode.componentInstance.$vuetify.theme
        let currentTheme = theme.currentTheme
        const defaultConf = {
            type: 'linear', // linear , radial
            deg: '60',
            color1: currentTheme.primary,
            color2: 'transparent'
        }
        let conf = { ...defaultConf, ...binding.value }
        if (conf.type == 'linear') {
            conf.prop = conf.deg + 'deg'
        }
        else conf.prop = 'circle'

        el.style.backgroundImage = `${conf.type}-gradient(${conf.prop},${conf.color1},${conf.color2})`
    }
})