<template>
  <v-container fill-height fluid justify-center>
    <v-hover v-slot="{ hover }">
      <v-sheet
        color="transparent"
        id="formSheet"
        :width="sheetWidth"
        class="pr-4 pl-4"
        :rounded="'xl'"
        @click="sheetFocus= true"
        :class="{'isFocus':sheetFocus|| hover}"
        v-click-outside="onClickOutside"
      >
        <img src="assets/images/icon.svg" width="80" class="d-flex ml-auto mr-auto pb-2" alt />
        <div class="white--text text-h5 text-center pb-2">小熊摇篮智能教育系统</div>
        <div class="text text-center login_t pb-3">Login to your account</div>

        <validation-observer ref="observer" v-slot="{ invalid }">
          <form @submit.prevent="submit">
            <validation-provider name="E-Mail" rules="required|email" v-slot="{ errors }">
              <v-text-field
                color="white"
                v-model="email"
                :error-messages="errors"
                label="E-mail"
                required
              ></v-text-field>
            </validation-provider>
            <validation-provider name="Password" rules="required" v-slot="{ errors }">
              <v-text-field
                color="white"
                v-model="password"
                :error-messages="errors"
                :counter="10"
                label="Password"
                required
                :type="showPW? 'text':'password'"
                :append-icon="showPW ? 'mdi-eye' : 'mdi-eye-off'"
                @click:append="showPW = !showPW"
              ></v-text-field>
            </validation-provider>

            <v-checkbox v-model="rememberme" label="Remember Me?"></v-checkbox>
            <v-expand-transition>
              <div v-if="rememberme">
                <p>Ok! I remembered!</p>
              </div>
            </v-expand-transition>

            <v-btn block class text-color="black" rounded large @click="submit" :disabled="invalid">
              <v-icon class="icon icon-pacifier"></v-icon>
              <v-icon class="icon icon-baby-boy-1 mr-2"></v-icon>Login
            </v-btn>
            <v-row>
              <v-col>
                <v-divider style="margin-top: 13px;"></v-divider>
              </v-col>
              <v-col cols="2" class="text-center">or</v-col>
              <v-col>
                <v-divider style="margin-top: 13px;"></v-divider>
              </v-col>
            </v-row>
            <v-btn block class text-color="black" rounded large @click="submit">
              <v-icon class="fab fa-weixin mr-2"></v-icon>Login with WeChat
            </v-btn>
          </form>
        </validation-observer>
      </v-sheet>
    </v-hover>
  </v-container>
</template>

<script>
import { required, max, email } from "vee-validate/dist/rules";
import {
  extend,
  ValidationObserver,
  ValidationProvider,
  setInteractionMode,
} from "vee-validate";
import { mapActions, mapGetters } from "vuex";

setInteractionMode("eager");

extend("required", {
  ...required,
  message: "{_field_} 不可為空",
});

extend("max", {
  ...max,
  message: "{_field_} 最多 {length} 字",
});

extend("email", {
  ...email,
  message: "{_field_} 格式不正確",
});

export default {
  components: {
    ValidationObserver,
    ValidationProvider,
  },
  data() {
    return {
      // Form Sheet
      sheetFocus: false,
      email: "",
      password: "",
      rememberme: false,
      showPW: false,
    };
  },
  computed: {
    ...mapGetters("auth", ["isAuthenticated"]),
    sheetWidth() {
      switch (this.$vuetify.breakpoint.name) {
        case "lg":
          return 600;
        case "xl":
          return 800;
        default:
          return 400;
        // case 'xs': return 220
        // case 'sm': return 400
        // case 'md': return 500
      }
    },
  },
  methods: {
    onClickOutside() {
      this.sheetFocus = false;
    },
    async submit() {
      let valid = await this.$refs.observer.validate();
      if (valid) {
        try {
          let login = await this.login({
            username: this.email,
            password: this.password,
          });
          if (login == true) {
            console.log("success login");
            this.$router.push("/");
          }
        } catch (err) {
          console.log(err);
        }
      }
    },
    ...mapActions("auth", ["login"]),
  },
  created() {
    if (this.isAuthenticated) {
      this.$router.push("/");
    }
  },
};
</script>

