import api from '../../api/wekids'

const retrySecond = 30000

const state = {
    accessToken: null,
    expiredTime: null,
    refreshToken: null,
    refreshExpiredTime: null,
}

const mutations = {
    authUser(state, newState) {
        state.accessToken = newState.accessToken;
        state.expiredTime = newState.expiredTime;
        state.refreshToken = newState.refreshToken;
        state.refreshExpiredTime = newState.refreshExpiredTime;
    },
    clearAuthData(state) {
        state.accessToken = null;
        state.expiredTime = null;
        state.refreshToken = null;
        state.refreshExpiredTime = null;
    }
}

const actions = {
    checkStorageAuthData({ commit, dispatch }) {
        const accessToken = localStorage.getItem('accessToken')
        if (!accessToken) { // Token not Exist, need to Login
            return
        }
        const expiredTime = localStorage.getItem('expiredTime')
        const refreshExpiredTime = localStorage.getItem('refreshExpiredTime')
        const refreshToken = localStorage.getItem('refreshToken')
        const now = new Date()
        if (now >= expiredTime) { // Access Token Exist, but Expired
            // Check Refresh Token
            if (now >= refreshExpiredTime && refreshToken !== null) {
                // Refresh Token also expired
                return
            }
            else {
                // Renew by Refresh Token
                dispatch('renewToken', refreshToken)
            }
        }
        else {
            // Access Token still valid, load to state
            commit('authUser', {
                accessToken,
                expiredTime,
                refreshToken,
                refreshExpiredTime
            })
            dispatch('setLogoutTimer', expiredTime)
        }
    },
    async renewToken({ state, dispatch }, refreshToken) {
        if (refreshToken == null) {
            refreshToken = state.refreshToken
        }
        try {
            let res = await api.refreshToken(refreshToken)
            dispatch('processAuthRes', res)
        }
        catch (err) {
            setTimeout(() => {
                dispatch('renewToken')
            }, retrySecond)
            throw new Error(err)
        }
    },
    setLogoutTimer({ dispatch }, expiredTime) {
        setTimeout(() => {
            console.log('token expired')
            dispatch('renewToken')
        }, expiredTime - (new Date()))
    },
    async login({ dispatch }, authFormData) {
        try {
            let res = await api.login(authFormData.username, authFormData.password)
            dispatch('processAuthRes', res)
            return true
        }
        catch (err) {
            throw new Error(err)
        }
    },
    processAuthRes({ commit, dispatch }, res) {
        const authData = {
            accessToken: res.data.token,
            expiredTime: new Date(res.data.expireTime),
            refreshToken: res.data.refreshToken,
            refreshExpiredTime: new Date(res.data.refreshExpireTime)
        }
        commit('authUser', authData)
        dispatch('setLogoutTimer', authData.expiredTime)
        localStorage.setItem('accessToken', authData.accessToken)
        localStorage.setItem('expiredTime', authData.expiredTime)
        localStorage.setItem('refreshToken', authData.refreshToken)
        localStorage.setItem('refreshExpiredTime', authData.refreshExpiredTime)
    },
    logout({ commit }) {
        commit('clearAuthData');
        localStorage.removeItem('accessToken')
        localStorage.removeItem('expiredTime')
        localStorage.removeItem('refreshToken')
        localStorage.removeItem('refreshExpiredTime')
    }
}

const getters = {
    isAuthenticated() {
        return state.accessToken !== null
    }
}

export default {
    namespaced: true,
    state,
    mutations,
    actions,
    getters
}