const state = {
    drawerOpen: false
}

const mutations = {
    toggleDrawer: (state, payload) => state.drawerOpen = payload ?? !state.drawerOpen
}

const actions = {
    toggleDrawer: ({ commit }, payload) => commit('toggleDrawer', payload)
}

const getters = {
    drawerOpen: () => state.drawerOpen,
}

export default {
    namespaced: true,
    state,
    mutations,
    actions,
    getters
}