<template>
  <v-app-bar app color="white" class="elevation-0 blur">
    <v-app-bar-nav-icon v-if="backBtn != true" @click.stop="isOpen = !isOpen"
    class="border" :ripple="{ class: 'red--text' }" ></v-app-bar-nav-icon>
    <v-icon v-if="backBtn == true" @click="pageBack">mdi-arrow-left</v-icon>
    <v-spacer></v-spacer>
    <span class="headline">{{title}}</span>
    <v-spacer></v-spacer>
    <v-menu v-if="false" bottom left offset-y>
      <template v-slot:activator="{ on, attrs }">
        <v-btn icon v-bind="attrs" v-on="on">
          <v-icon>mdi-dots-vertical</v-icon>
        </v-btn>
      </template>

      <v-list>
        <v-list-item>
          <v-list-item-title>About</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
  </v-app-bar>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  props: {
    title: String,
    backBtn: Boolean,
  },
  computed: {
    ...mapGetters("layout", ["drawerOpen"]),
    isOpen: {
      get() {
        return this.drawerOpen;
      },
      set(val) {
        this.toggleDrawer(val);
      },
    },
  },
  methods: {
    ...mapActions("layout", ["toggleDrawer"]),
    pageBack() {
      this.$router.go(-1);
    },
  },
};
</script>

<style scoped>
.blur {
  -webkit-backdrop-filter: saturate(180%) blur(10px) !important;
  backdrop-filter: saturate(180%) blur(10px) !important;
  background: rgba(255, 255, 255, 0.6) !important;
}
</style>