import '@fortawesome/fontawesome-free/css/all.css'
import Vue from 'vue';
import Vuetify from 'vuetify/lib';

Vue.use(Vuetify);

import en from 'vuetify/es5/locale/en'
import enCustom from '../lang/en.json'
import zhHant from 'vuetify/es5/locale/zh-Hant'
import zhHantCustom from '../lang/zhHant.json'
import zhHans from 'vuetify/es5/locale/zh-Hans'
import zhHansCustom from '../lang/zhHans.json'

import Pacifier from '../components/Icons/Pacifier.vue'

export default new Vuetify({
    lang: {
        locales: {
            zhHans: {
                ...zhHans,
                ...zhHansCustom
            },
            zhHant: {
                ...zhHant,
                ...zhHantCustom
            },
            en: {
                ...en,
                ...enCustom
            }
        },
        current: 'en'
    },
    theme: {
        themes: {
            light: {
                default: '#7e6d61',
                primary: '#FFA19F',
                secondary: '#9e8888',
                info: '#00cae3',
                success: '#3fbe8f',
                accent: '#8c9eff',
                warning: '#ff9563',
                error: '#ff5252',
                tertiary: "#fa624d",
                quaternary: "#6A3F12",
                background: "#efebe8"
            }
        }
    },
    icons: {
        values: {
            pacifier: {
                component: Pacifier
            }
        }
    }
});
