import axios from 'axios'

const instance = axios.create({
    // baseURL: 'http://192.168.128.243:8080/api' // devServer, DO NOT use it
    baseURL: '/api' // localhost by default
})

export default {
    login(username, password) {
        return instance.post('/Auth/SignIn', {
            username,
            password
        })
    },
    refreshToken(refreshToken) {
        return instance.post('/Auth/RefreshToken', { refreshToken })
    },
}